$(document).ready(function() {

    //Accordion
    if (window.matchMedia("(min-width: 993px)").matches) {
        $('.item-show').hover(function () {
            $(this).parents('.item').addClass('item--active')
            $(this).parents('.item').siblings('.item').removeClass('item--active')
            $(this).parents('.item').siblings('.item').find('.item-hidden').removeClass('item-hidden--active')
            $(this).toggleClass('item-show--active')
            $(this).siblings('.item-hidden').addClass('item-hidden--active')
        })
        let itemShow = $('.item-show')
        let itemHidden = $('.item-hidden')

        $(document).click(function (e) {
            if ( !itemShow.is(e.target) && itemShow.has(e.target).length === 0 &&
                !itemHidden.is(e.target) && itemHidden.has(e.target).length === 0

            ) {
                itemHidden.removeClass('item-hidden--active')
                itemHidden.parents('.item').removeClass('item--active')
            }
        })
    }
    if (window.matchMedia("(max-width: 992px)").matches) {
        $(".item-show").click((function() {
            $(this).parents('.item').toggleClass("item--active")
            $(this).siblings(".item-hidden").slideToggle()
        }));
    }

    $('.map__item-close').click(function () {
        $(this).parents('.map__item').removeClass('item--active')
        $(this).parents('.map__item').find('.item-hidden').removeClass('item-hidden--active')
    })

    //Map-iframe__item
    $('.map-iframe__item-show').click(function () {
        $(this).addClass('active-show')
        $(this).siblings('.map-iframe__item-hidden').addClass('active-hidden')
    })

    $('.map-iframe__item-close').click(function () {
        $('.map-iframe__item-show').removeClass('active-show')
        $('.map-iframe__item-hidden').removeClass('active-hidden')
    })

    // Input-file
    const dt = new DataTransfer();
    $("#attachment").on('change', function(e){
        for(var i = 0; i < this.files.length; i++){
            let fileBloc = $('<span/>', {class: 'file-block'}),
                fileName = $('<span/>', {class: 'name', text: this.files.item(i).name});
            fileBloc.append('<span class="file-delete"><span>+</span></span>')
                .append(fileName);
            $("#filesList > #files-names").append(fileBloc);
        };
        for (let file of this.files) {
            dt.items.add(file);
        }
        this.files = dt.files;

        $('span.file-delete').click(function(){
            let name = $(this).next('span.name').text();
            $(this).parent().remove();
            for(let i = 0; i < dt.items.length; i++){
                if(name === dt.items[i].getAsFile().name){
                    dt.items.remove(i);
                    continue;
                }
            }
            document.getElementById('attachment').files = dt.files;
        });
    });


    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})